import { Component, OnDestroy, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';

import { AlertConfig, AlertController } from './alert-controller.service';

@Component({
    selector: 'ui-alert',
    template: `
      <ui-dialog [show]="show" (hide)="hide()"><div class="ui-alert"><h3 class="ui-alert-title" *ngIf="title" [innerHTML]="title"></h3><div class="ui-alert-body" [innerHTML]="content"></div><div class="ui-alert-footer text-right"><button (click)="checked()" type="button" class="btn lg block">{{ btnText || '确定' }}</button></div></div></ui-dialog>
    `
})
export class AlertComponent implements OnInit, OnDestroy {
    show: boolean = false;

    title: string = '';
    content: string = '';
    btnText: string = '';

    private sub: Subscription;

    constructor(private alertController: AlertController) {
    }

    ngOnInit() {
        // 订阅alert事件
        this.sub = this.alertController.alertConfig$.subscribe((params: AlertConfig) => {
            // 设置状态，以弹出对话框
            this.show = true;

            this.title = params.title;
            this.content = params.content;
            this.btnText = params.btnText;
        });
    }

    ngOnDestroy() {
        this.sub.unsubscribe();
    }

    checked() {
        // 当点击确认按扭时关闭弹窗
        this.show = false;
    }

    hide() {
        // 当弹窗动画完成时，发布事件
        this.alertController.publishAction();
    }
}


