import { Directive, ElementRef, EventEmitter, OnDestroy, OnInit, Output, Input } from '@angular/core';
import 'hammerjs';

@Directive({
    selector: '[uiSwipeLeft]'
})
export class SwipeLeftDirective implements OnInit, OnDestroy {
    @Output()
    uiSwipeLeft = new EventEmitter<any>();

    @Input()
    uiSwipeLeftOptions: HammerOptions = {};

    private hammerInstance: HammerManager;

    constructor(private elementRef: ElementRef) {
    }

    ngOnInit() {
        let element = this.elementRef.nativeElement;
        this.hammerInstance = new Hammer(element);
        this.hammerInstance.set(this.uiSwipeLeftOptions);
        this.hammerInstance.on('swipeleft', (event: any) => {
            this.uiSwipeLeft.emit(event);
        });
    }

    ngOnDestroy() {
        this.hammerInstance.off('swipeleft');
    }
}


