import { Component, EventEmitter, Input, Output } from '@angular/core';

@Component({
    selector: 'ui-dialog',
    template: `
      <ui-mask [show]="show" (hide)="animationEnd()"><div class="ui-dialog" *ngIf="display" (animationend)="h()" [ngClass]="show ? 'in' : 'out'"><ng-content></ng-content></div></ui-mask>
    `
})
export class DialogComponent {
    @Input()
    set show(value: boolean) {
        this._show = value;
        if (value) {
            this.display = true;
        }
    }

    get show() {
        return this._show;
    }

    @Output()
    hide = new EventEmitter<void>();

    display: boolean = false;

    private _show: boolean = false;

    animationEnd() {
        this.hide.emit();
        this.display = false;
    }

    h() {
        if (!this.show) {
            this.display = false;
        }
    }
}


