import { Component, OnDestroy, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';

import { ConfirmConfig, ConfirmController } from './confirm-controller.service';

@Component({
    selector: 'ui-confirm',
    template: `
      <ui-dialog [show]="show" (hide)="hide()"><div class="ui-confirm"><h3 class="ui-confirm-title clearfix" *ngIf="title" [innerHTML]="title"></h3><div class="ui-confirm-body" [innerHTML]="content"></div><div class="ui-confirm-footer text-right"><button (click)="checked(false)" type="button" class="btn lg block">{{ btnsText[0] }}</button> <button (click)="checked(true)" type="button" class="btn lg block">{{ btnsText[1] }}</button></div></div></ui-dialog>
    `
})

export class ConfirmComponent implements OnInit, OnDestroy {
    show: boolean = false;

    title: string = '';
    content: string = '';
    btnsText: Array<any> = ['取消', '确认'];
    result: boolean = false;

    private sub: Subscription;

    constructor(private confirmService: ConfirmController) {
    }

    ngOnInit() {
        // 订阅用户事件
        this.sub = this.confirmService.confirmConfig$.subscribe((params: ConfirmConfig) => {
            // 设置动画状态
            this.show = true;

            // 赋值相应参数
            this.title = params.title;
            this.content = params.content;
            if (params.btnsText) {
                this.btnsText = params.btnsText;
            }

        });
    }

    ngOnDestroy() {
        this.sub.unsubscribe();
    }

    checked(result: boolean) {
        // 当用户点击按扭时，关闭弹窗
        this.show = false;
        this.result = result;
    }

    hide() {
        // 当弹窗关闭动画完成时，发布相应事件
        this.confirmService.publishAction(this.result);
    }
}


